.commentsWrap {
  display: flex;
  column-gap: 60px;
  row-gap: 50px;
  margin: 0 auto;
  height: 100%;
}

.commentsPost {
  min-height: auto;
  padding-top: 0;
  width: 100%;
}

.commentsList {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  overflow: auto;
  height: 100%;
}

.commentsActionWrap {
  display: flex;
  row-gap: 30px;
  margin-bottom: auto;
  flex-direction: column;
  background-color: #fff;
  padding: 15px;
  border-radius: 15px;
  width: 100%;
  max-height: 100%;
}

@media (max-width: 1437px) {
  .commentsWrap {
    flex-wrap: wrap;
    height: auto;
    padding-bottom: 20px;
  }

  .commentsPost {
    max-width: 100%;
  }
}
