.modalCommentsBackground {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(109, 128, 128, 0.6);
}

.modalCommentsWrapper {
  width: 100%;
  padding: 10vh 3vw 0;
  border-radius: 4px;
  max-height: 80vh;
  height: 100%;
}

@media (max-width: 1437px) {
  .modalCommentsWrapper {
    max-height: 100%;
    overflow-y: auto;
  }
}
